// Variables
$base-color-dark: #000;
$base-color-light: #fff;

html, body {
  background: $base-color-dark;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

a {
  transition: all .1s ease-out;
}

section {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  align-items: center;
  background: $base-color-light;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  &:hover {
    background-color: $base-color-dark;
    a {
      color: $base-color-light;
    }
  }
  p {
    font-size: 8vw;
    font-weight: 600;
    margin: 0;
  }
  a {
    color: $base-color-dark;
  }
}

main {
  align-items: center;
  border-bottom: 1px dotted $base-color-light;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  padding: 10em 0;
  > div {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
  }
}

button {
  background: $base-color-light;
  border: 1px solid;
  margin: .6em 0;
  min-width: 10rem;
  padding: 1em 1em;
  text-transform: capitalize;
  a {
    color: $base-color-dark;
  }
  &:hover {
    background: $base-color-dark;
    border: 1px dotted $base-color-light;
    color: $base-color-light;
    a {
      color: $base-color-light;
    }
  }
}

footer {
  justify-content: center;
  color: $base-color-light;
  display: flex;
  font-size: 1em;
  text-transform: capitalize;
}